@import "./variables.css";

.types_list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	gap: 25px;
	margin-top: 25px;
}

.types_list li {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 151px;
	height: 45px;
	color: white;
	text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.644);
	box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.645);
	font-weight: bolder;
	text-transform: uppercase;
	letter-spacing: 2px;
	border-radius: 25px 0 25px 0;
	font-size: 15px;
	overflow: hidden;
	position: relative;
}

.types_list li::before {
	content: "";
	height: 100%;
	width: 100%;
	border-radius: 25px 0 25px 0;
	box-shadow: -8px -8px 20px rgba(0, 0, 0, 0.447);
	position: absolute;
	left: 5px;
	top: 5px;
}

