.poke_preview {
	grid-row: 1;
	grid-column: 3;
	align-self: center;
	justify-self: center;
	max-width: 100%;
	max-height: 490px;
	object-fit: contain;
	opacity: 1;
	transition: all .7s ease-in-out;
}

@media (max-width: 1000px) {
	.poke_preview {
		grid-row: 2;
		grid-column: 1 / 3;
		align-self: flex-end;
		justify-self: flex-start;
	}
}

@media (max-width: 770px) {
	.poke_preview {
		grid-row: 2;
		grid-column: 1;
		align-self: flex-end;
		justify-self: center;
	}
}
